import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { idService } from '../../providers/idService/idService';
import { DatabaseProvider } from '../../providers/database/database';
/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
	id: number;
	nombre: string;
	user: boolean = false;
	pass: boolean = false;
	newPass: string;
	newUsername: string;
  constructor(public navCtrl: NavController, public navParams: NavParams,public idService: idService,private database: DatabaseProvider) {
  	this.database.getUsuario(this.idService.usuID).then((data) => {
  		for(let i in data){
  			this.nombre = data[i].nombre;
  		}
  		this.id = this.idService.usuID;
  	}, (error) => {
  		console.log(error);
  	})
  }

  showUser(){
  	this.user = true;
  	this.pass = false;
  	this.newPass = "";
  }

  showPass(){
  	this.user = false;
  	this.pass = true;
  	this.newUsername = "";
  }
  update(){
  	if(this.newUsername != null){
  		this.database.updateUsername(this.id,this.newUsername).then((data) => {
  			for(let i in data){
  				this.nombre = data[i].nombre;
  			}
  		})
  	}else if(this.newPass != null){
  		this.database.updatePassword(this.id,this.newUsername).then((data) => {
  		console.log(data);	
  		}, (error) => {
  			console.log(error);
  		})
  	}
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProfilePage');
  }

}



